<template>
    <div class="index">
        <div class="clearfix">
            <div class="card my-profile">
                <p class="lager-font bold">我的资料：</p>
                <div class="profile clearfix row">
                    <div class="col-4">
                        <img v-if="userinfo.head" class="avatar" :src="userinfo.head|img_mapi" alt="">
                        <img v-else class="avatar" :src="userinfo.head" alt="">
                    </div>
                    <div class="col-8">
                        <p class="bold">{{userinfo.username}}</p>
                        <div>
                            <router-link to="address">收货地址</router-link>
                            <router-link to="safe">修改密码</router-link>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card my-money">
                <p class="lager-font bold">我的资产</p>
                <div class="money clearfix row">
                    <div class="col-12">
                        <p>我的余额 <span
                                style="font-size: 28px;line-height: 30px;font-weight: bold;color: #ff0077">{{exchange=='10'?(userinfo.point/exchange).toFixed(2):(userinfo.point/exchange).toFixed(0)}}</span>
                            {{unit}}</p>
                    </div>
                    <div class="col-12 row" style="margin-top: 24px">
                        <div class="col-4">
                            <router-link class="ac" to="money/present">余额转赠</router-link>
                        </div>
                         <div class="col-4">
                            <router-link class="ac" to="money/exchange">卡券兑换</router-link>
                        </div>
                        <div class="col-4">
                            <a class="ac" :href="`/grade.html?can=1`">我能购买</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="my-order">
            <div class="my-order-head">
                <span class="bold">我的订单</span>
                <router-link to="order" class="fr">全部订单 <i class="icon-r"></i></router-link>
            </div>
            <div class="my-order-detail clearfix">
                <div class="order-filter fl ac">
                    <router-link :to="{path:'order',query:{tab:2}}">待支付({{storage.getStorage('fft-order-nums').orderNums.noPayNum}})</router-link>
                </div>
                <div class="order-filter fl ac">
                    <router-link :to="{path:'order',query:{tab:3}}">待发货({{storage.getStorage('fft-order-nums').orderNums.noSendNum}})</router-link>
                </div>
                <div class="order-filter fl ac">
                    <router-link :to="{path:'order',query:{tab:4}}">待收货({{storage.getStorage('fft-order-nums').orderNums.noReceiveNum}})</router-link>
                </div>
                <div class="order-filter fl ac">
                    <router-link to="order">退款/售后</router-link>
                </div>
            </div>
        </div>
        <div class="order-list">
            <div v-if="order_preview.length>0" class="order-list-item clearfix" v-for="(i,k) in order_preview">
                <img class="avatar fl" :src="i.goods_child[0].default_img | img" alt="avatar">
                <p class="fl" style="    margin-top: 18px;
    margin-left: 10px;">您提交了订单，等待付款</p>
                <div class="fr" style="margin-top: 18px;
    margin-right: 5px;">
                    <router-link class="link" :to="{path:'order/detail',query:{order_sn:i.order_sn}}">查看详情</router-link>
                    <router-link class=" order-btn" :to="{path:'paytable',query:{order_sn:i.order_sn}}">立即付款</router-link>
                </div>
            </div>
            <div v-else class="ac" style="padding: 50px;
            background-color: rgba(0,0,0,0)">
                <img src="../assets/img/nothing/order.png" alt="">
                <p>怎么回事？居然一个订单也没有，赶紧去<a href="/grade.html" style="color: #66ccff">商品中心</a>买买买吧！</p>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    };
    import {mapState} from 'vuex'
    export default {
        name: "index",
        metaInfo: {
            title: '个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                userinfo: {},
                exchange:1,
                unit:'',
                order_preview:[]
            }
        },
        computed:{
            ...mapState('order', ['start_time','end_time']),
        },
        methods: {
            getMemberInfo() {
                this.yum('api/member/getMemberInfo').then(res => {
                    if (res.data.status === 200) {
                        this.userinfo = res.data.data;
                        this.exchange = parseInt(res.data.exchange);
                        this.unit = res.data.unit;
                    }
                })
            },
            getOrderListss(){
                let data = {
                    page:1,
                    limit:5,
                    status:1,
                    start_time:Date.parse(this.start_time)/1000,
                    end_time:Date.parse(this.end_time)/1000+57600,
                    shipping:0,
                    pay_status:0
                };
                this.yum('api/order/listss',data).then(res=>{
                    if (res.data.status===200){
                        this.order_preview = res.data.data
                    }
                })
            }
        },
        async created() {
            this.$store.dispatch('layout/Change', display);
            if (this.storage.hasItem('FFT')) {
                this.getMemberInfo()
            };
            this.getOrderListss()
        },

    }
</script>

<style scoped lang="stylus">
    .card
        width 400px
        height 140px
        float left
        padding 20px 16px 16px 20px
        background-color white
        box-shadow 0 0 8px #bbb
        &:last-child
            margin-left 20px
        p
            color black
        .profile
            margin-top 10px
            position relative
            padding-left 80px
            p
                margin-top 10px
                margin-bottom 14px
            a:last-child
                margin-left 20px
        .money
            position relative
            padding-left 80px
            a
                width 80px
                height 31px
                line-height 31px
                display block
                border-radius 15px
                border 1px solid #555
                color #555555
                &:hover
                    color white
                    border-color #ff0077
                    background-color #ff0077
        .avatar
            width 80px
            height 80px
            border-radius 50%

    .my-order
        position relative
        margin-top 20px
        background-color white
        box-shadow 0 0 8px #bbb
        &-head
            position relative
            height 40px
            padding 10px 20px
            span.bold
                color black
            a
                .icon-r
                    width 6px
                    height 12px
                    display inline-block
                    background-image url("../../usercenter/assets/img/index_arrow_default.png")
                &:hover .icon-r
                    background-image url("../../usercenter/assets/img/index_arrow_hover.png")
        &-detail
            height 40px
            border-top 1px solid #ccc
            .order-filter
                width 150px
                height 40px
                line-height 40px
                &:after
                    content '|'
                    float right
                    color #ccc
                &:last-child:after
                    display none

    .order-list
        margin-top 20px
        background-color rgba(0,0,0,0)
        &-item
            height 100px
            padding 20px
            background-color white
            margin-bottom 20px
            box-shadow 0 0 8px #bbb
            .avatar
                display block
                width 60px
                height 60px
            .link
                margin-right 5px
                &:after
                    content ''
                    margin-left 5px
                    vertical-align middle
                    border 1px solid #ccc
                    height 14px
                    width 0
                    display inline-block
            .order-btn
                padding 5px 6px
                background-color orangered
                color white
                border-radius 6px
</style>